:root {
    --backgroundColor: #171717;
    --secondaryBackgroundColor: #383838;
    --titleColor: white;
    --actionButtonBackgroundColor: #2b2b2b;
    --actionButtonTextColor: white;
    --cardPlateBackgroundColor: #2b2b2b;
}

body {
    box-sizing: border-box;
}

#app {
    height: 100vh;
    width: 100vw;
    transition: background-color 1s ease;
    background-color: var(--backgroundColor);
    display: flex;
    flex-direction: column;
}
.flex-x-center {
    display: flex;
    flex-direction: column;
    align-items: center;
}
#chart-wrapper {
    position: relative;
    width: 96vw;
    height: 50vh;
    background-color: transparent;
    margin: 1rem;
    display: flex;
    overflow: auto;
    flex-direction: row;
    justify-content: start;
    align-items: end;
}
#controller-bar {
    /*position: relative;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 1rem;
    width: 96vw;
    height: 50vh;
    transition: background-color 1s ease;
    background-color: var(--secondaryBackgroundColor);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
#navbar {
    width: 100vw;
    height: 4rem;
    background-color: transparent;
    border-bottom: 1px solid #383838;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
#navbar-container {
    width: 96vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
#nav-title {
    font-weight: bolder;
    /*color: white;*/
    transition: color 1s ease;
    color: var(--titleColor);
}

#dropdown-section {
    display: flex;
    flex-direction: row;
}
.dropdown-container {
   position: relative;
    margin-right: 5px;
}
.dropdown-menu {
    cursor: pointer;
    box-sizing: border-box;
    transition: background-color 1s ease, color 1s ease;
    color: var(--titleColor);
    background-color: var(--cardPlateBackgroundColor);
    /*color: aliceblue;*/
    padding: 8px;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.dropdown-options {
    position: absolute;
    z-index: 10;
    top: 4rem;
    min-width: 6rem;
    /*height: 6rem;*/
    transition: background-color 1s ease, color 1s ease;
    color: var(--titleColor);
    background-color: var(--cardPlateBackgroundColor);
    border-radius: 5px;
}
.option {
    list-style: none;
    text-align: left;
    color: inherit;
    padding: 8px
}
.option:hover {
    background-color: #171717;
    color: white;
}
.h-3 {
    height: 20px;
}
.w-3 {
    width: 20px;
}
.m-1 {
    margin: 3px;
}

.bar {
    /*display: flex;*/
    background-color: #ebe64d;
    min-width: 1px;
    flex-grow: 1;
    transition: border 1s ease;
    border: 1px solid var(--backgroundColor);
    transition: height 100ms;
}

.debug-bar {
    /*position: absolute;*/
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: auto;
}

#play-btn {
    height: 5rem;
    width: 5rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: none;
    /*background-color: #2b2b2b;*/
    /*background-color: transparent;*/
    /*background-color: var(--actionButtonBackgroundColor);*/
    transition: background-color 1s ease, color 1s ease;
    color: var(--actionButtonTextColor);
    background-color: var(--backgroundColor);
    font-size: large;
    border-radius: 5rem;
    padding: 8px;
    margin: 10px;
}
.secondary-btn {
    height: 3rem;
    width: 3rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: none;
    /*background-color: #2b2b2b;*/
    /*background-color: transparent;*/
    /*background-color: var(--actionButtonBackgroundColor);*/
    transition: background-color 1s ease, color 1s ease;
    color: var(--actionButtonTextColor);
    background-color: var(--backgroundColor);
    font-size: large;
    border-radius: 3rem;
    padding: 8px;
    margin: 10px;
}
.debug-btn:hover {
    color: white;
}

#legend {
    position: absolute;
    top: 4rem;
    left: 1rem;
    color: #9e31f7;
    display: flex;
    flex-direction: column;
}

#dock {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
#operation-plate {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 1rem;
    transition: background-color 1s ease, color 1s ease;
    color: var(--titleColor);
    background-color: var(--cardPlateBackgroundColor);

    padding: 1rem;
}
.operation {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin: 1px 1rem;
    color: inherit;
}
.colorPlate {
    width: 1rem;
    height: 1rem;
}
#learn-more {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 1rem;
    transition: background-color 1s ease;
    background-color: var(--cardPlateBackgroundColor);
    padding: 1rem;
    border-radius: 5px;
    color: #fff024;
    cursor: pointer;
}
.themeBtn {
    min-width: 6rem;
    transition: background-color 1s ease, color 1s ease;
    color: var(--titleColor);
    background-color: var(--cardPlateBackgroundColor);
    border-radius: 5px;
    border:none;
    margin-right: 3px;
    /*color: aliceblue;*/
    font-size: 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}